export interface ICodeAssistantResponse {
    refactorResponse: IRefactorResponse,
    explainResponse: IExplainResponse,
    responseError: {
        error: string;
    }
}

export interface IRefactorResponse {
    refactorResponse: Array<IRefactorPiece>;
}

export interface IExplainResponse {
    explainResponse: string;
}

export interface IRefactorPiece {
    refactorId: number;
    codePieceBeforeRefactor: string;
    codePieceAfterRefactor: {
        refactoredCodeValue: string;
        reason: string;
        startLineNumber?: number;
        endLineNumber?: number;
    };
}